.SuperButton_btn__3MgTu {
    position: relative;
    margin: 0 5px 10px 0;
    padding: 5px 20px;
    background: none;
    border: 4px solid;
    border-image: linear-gradient(to right, #16a085, #2ecc71);
    border-image-slice: 1;
    color: #1abc9c;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
    font-size: 14px;
}

.SuperButton_btn__3MgTu::before,
.SuperButton_btn__3MgTu::after {
    content: "";
    position: absolute;
    width: 4px;
    height: 5px;
    background: #2c3e50;
    transform: skewX(50deg);
    transition: .4s linear;
}

.SuperButton_btn__3MgTu::before {
    top: -4px;
    left: 10%;
}

.SuperButton_btn__3MgTu::after {
    bottom: -4px;
    right: 10%;
}

.SuperButton_btn__3MgTu:hover::before {
    left: 80%;
}

.SuperButton_btn__3MgTu:hover::after {
    right: 80%;
}

.SuperButton_btn__3MgTu:active {
    transition: .2s linear;
    background: rgba(46, 204, 113, 0.2);
}

.SuperButton_btn__3MgTu:focus {
    outline: #e67e22 solid 1px;
}
